'use client';

import { useState, useEffect } from 'react';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Skeleton } from '@/components/ui/skeleton';
import { getIndicatorData } from '@/lib/actions';
import type { IndicatorData } from '@/lib/indicators';
import {
  Gauge,
  TrendingUp,
  TrendingDown,
  Waves,
  Percent,
  BarChart2,
  GitCommitHorizontal,
  MoveHorizontal,
  Orbit,
  Cloud,
  Sigma,
  ChevronsUpDown,
  Zap,
} from 'lucide-react';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Separator } from '@/components/ui/separator';

interface IndicatorSuiteProps {
  symbol: string;
  timeframe: string;
}

const getSentiment = (value: number, upper: number, lower: number) => {
  if (value > upper) return { text: 'Overbought', color: 'text-chart-5' };
  if (value < lower) return { text: 'Oversold', color: 'text-chart-2' };
  return { text: 'Neutral', color: 'text-muted-foreground' };
};

const getBullishBearish = (value1: number, value2: number) => {
  if (value1 > value2) return { text: 'Bullish', color: 'text-chart-2', icon: <TrendingUp className="h-4 w-4" /> };
  return { text: 'Bearish', color: 'text-chart-5', icon: <TrendingDown className="h-4 w-4" /> };
};

const getSupertrendSentiment = (direction: 'buy' | 'sell' | undefined) => {
    if (direction === 'buy') return { text: 'Buy Signal', color: 'text-chart-2', icon: <TrendingUp className="h-4 w-4" /> };
    if (direction === 'sell') return { text: 'Sell Signal', color: 'text-chart-5', icon: <TrendingDown className="h-4 w-4" /> };
    return { text: 'Neutral', color: 'text-muted-foreground', icon: <ChevronsUpDown className="h-4 w-4" /> };
}

export function IndicatorSuite({ symbol, timeframe }: IndicatorSuiteProps) {
  const [data, setData] = useState<IndicatorData | null>(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const fetchData = async () => {
      setIsLoading(true);
      const indicatorData = await getIndicatorData(symbol, timeframe);
      setData(indicatorData);
      setIsLoading(false);
    };

    fetchData();
  }, [symbol, timeframe]);
  
  const rsiSentiment = data?.rsi ? getSentiment(data.rsi.rsi, 70, 30) : null;
  const macdSentiment = data?.macd ? getBullishBearish(data.macd.macd, data.macd.signal) : null;
  const stochasticSentiment = data?.stochastic ? getSentiment(data.stochastic.k, 80, 20) : null;
  const williamsRSentiment = data?.williamsR ? getSentiment(data.williamsR.williamsR, -20, -80) : null;
  const cciSentiment = data?.cci ? getSentiment(data.cci.cci, 100, -100) : null;
  const mfiSentiment = data?.mfi ? getSentiment(data.mfi.mfi, 80, 20) : null;
  const aoSentiment = data?.awesomeOscillator && data.awesomeOscillator.ao > 0 
    ? { text: 'Bullish', color: 'text-chart-2', icon: <TrendingUp className="h-4 w-4" /> }
    : { text: 'Bearish', color: 'text-chart-5', icon: <TrendingDown className="h-4 w-4" /> };
  const supertrendFastSentiment = getSupertrendSentiment(data?.supertrendFast?.direction);
  const supertrendSlowSentiment = getSupertrendSentiment(data?.supertrendSlow?.direction);


  const renderIndicator = (
    icon: React.ReactNode,
    name: string,
    value: string | number | undefined,
    sentiment?: { text: string; color: string } | null,
    details?: React.ReactNode
  ) => (
    <div className="flex items-center justify-between text-sm">
      <div className="flex items-center gap-2">
        {icon}
        <span className="font-semibold">{name}</span>
      </div>
      <div className="text-right">
        {value !== undefined && <p className="font-bold">{typeof value === 'number' ? value.toFixed(2) : value}</p>}
        {sentiment && <p className={`text-xs ${sentiment.color}`}>{sentiment.text}</p>}
        {details}
      </div>
    </div>
  );

  return (
    <Card>
      <CardHeader>
        <CardTitle>Indicator Suite</CardTitle>
      </CardHeader>
      <CardContent className="p-0">
        <ScrollArea className="h-[400px] p-4">
          {isLoading ? (
            <div className="space-y-4">
              {Array.from({ length: 12 }).map((_, i) => (
                <Skeleton key={i} className="h-8 w-full" />
              ))}
            </div>
          ) : data ? (
            <div className="space-y-4">
              {renderIndicator(supertrendFastSentiment.icon, 'Supertrend (10, 2)', data.supertrendFast?.supertrend, supertrendFastSentiment)}
              <Separator />
              {renderIndicator(supertrendSlowSentiment.icon, 'Supertrend (20, 3)', data.supertrendSlow?.supertrend, supertrendSlowSentiment)}
              <Separator />
              {renderIndicator(<Gauge className="h-5 w-5 text-muted-foreground" />, 'RSI (14)', data.rsi?.rsi, rsiSentiment)}
              <Separator />
              {renderIndicator(macdSentiment?.icon || <ChevronsUpDown className="h-5 w-5 text-muted-foreground" />, 'MACD (12, 26, 9)', data.macd?.macd, macdSentiment)}
              <Separator />
              {renderIndicator(<Waves className="h-5 w-5 text-muted-foreground" />, 'Bollinger (20, 2)', undefined, null, (
                <div className="font-mono text-xs">
                  <p>U: {data.bollingerBands?.upper.toFixed(2)}</p>
                  <p>M: {data.bollingerBands?.middle.toFixed(2)}</p>
                  <p>L: {data.bollingerBands?.lower.toFixed(2)}</p>
                </div>
              ))}
              <Separator />
              {renderIndicator(<Percent className="h-5 w-5 text-muted-foreground" />, 'Stochastic (14, 3)', data.stochastic?.k, stochasticSentiment)}
              <Separator />
              {renderIndicator(<BarChart2 className="h-5 w-5 text-muted-foreground" />, 'ATR (14)', data.atr?.atr)}
              <Separator />
              {renderIndicator(<GitCommitHorizontal className="h-5 w-5 text-muted-foreground" />, 'OBV', data.obv?.obv.toExponential(2))}
              <Separator />
              {renderIndicator(<MoveHorizontal className="h-5 w-5 text-muted-foreground" />, 'Williams %R (14)', data.williamsR?.williamsR, williamsRSentiment)}
              <Separator />
              {renderIndicator(<Orbit className="h-5 w-5 text-muted-foreground" />, 'CCI (20)', data.cci?.cci, cciSentiment)}
              <Separator />
              {renderIndicator(<TrendingUp className="h-5 w-5 text-muted-foreground" />, 'ROC (14)', data.roc?.roc)}
               <Separator />
              {renderIndicator(<Sigma className="h-5 w-5 text-muted-foreground" />, 'MFI (14)', data.mfi?.mfi, mfiSentiment)}
               <Separator />
              {renderIndicator(aoSentiment?.icon || <ChevronsUpDown className="h-5 w-5 text-muted-foreground" />, 'Awesome Oscillator', data.awesomeOscillator?.ao, aoSentiment)}
              <Separator />
               {renderIndicator(<Cloud className="h-5 w-5 text-muted-foreground" />, 'Ichimoku Cloud', undefined, null, (
                <div className="font-mono text-xs">
                  <p>Tenkan: {data.ichimokuCloud?.tenkanSen.toFixed(2)}</p>
                  <p>Kijun: {data.ichimokuCloud?.kijunSen.toFixed(2)}</p>
                  <p>Span A: {data.ichimokuCloud?.senkouSpanA.toFixed(2)}</p>
                  <p>Span B: {data.ichimokuCloud?.senkouSpanB.toFixed(2)}</p>
                </div>
              ))}
              <Separator />
              {renderIndicator(<TrendingUp className="h-5 w-5 text-muted-foreground" />, 'SMA (20)', data.sma?.sma)}

            </div>
          ) : (
            <p className="text-muted-foreground text-center">Could not load indicator data.</p>
          )}
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
